#header {
  position: relative;
  display: flex;
  justify-content: center;
  width: 100%;
  margin: 0 auto;
  margin-bottom: 10px;
  .logo {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 80px;
    height: 80px;
    border-bottom-left-radius: 30px;
    background-color: var(--mbgc);
    img {
      width: 50px;
      height: 50px;
    }
  }

  .title {
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 1;
    flex-basis: 160px;
    height: 80px;
    background-color: var(--mbgc);
    overflow: hidden;
    a {
      width: 100%;
      height: 60px;
      background: url("../../image/title.JPG") no-repeat center;
      background-size: contain;
    }
  }

  .nav {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 80px;
    height: 80px;
    border-bottom-right-radius: 30px;
    background-color: var(--mbgc);
    cursor: pointer;
    .listicon {
      font-size: 30px;
      color: #fff;
    }
  }

