.menubar {
  position: fixed;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 9999;
  transition: 1s;
  .menuleft {
    width: 30%;
    height: 100%;
    background-color: rgba(255, 255, 255, 0.3);
    .btn {
      position: absolute;
      left: 50px;
      top: 50px;
      width: 50px;
      height: 50px;
      font-size: 30px;
    }
  }
  .menuright {
    width: 70%;
    height: 100%;
    border-left: 1px solid #555;
    box-sizing: border-box;
    background-color: var(--mbgc);
    .loginstate {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 80px;
      font-size: 20px;
      a:nth-child(1) {
        flex: 2;
        text-align: center;
        .icon {
          font-size: 30px;
        }
      }
      .loginms {
        flex: 6;
        text-align: center;
      }
      a:nth-child(3) {
        text-align: center;
        flex: 2;
        .icon {
          font-size: 30px;
        }
      }
    }

    .login_sign {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 50px;
      background-color: var(--sbgc);
      & > div {
        flex: 1;
        width: 100%;
        height: 50px;
        line-height: 50px;
        text-align: center;
        border-top: 1px solid #333;
        border-right: 1px solid #333;
        border-bottom: 1px solid #333;
        box-sizing: border-box;
      }
      .signbtn {
        border-right: none;
      }
    }

    .menulist {
      position: relative;
      width: 100%;
      height: 100%;
      background-color: var(--sbgc);
      .mainli {
        width: 100%;
        height: 50px;
        &.on {
          background-color: var(--ssbgc);
        }
        a {
          width: 30%;
          height: 100%;
          font-size: 14px;
          line-height: 50px;
          text-align: center;
          border-right: 1px solid #555;
          border-bottom: 1px solid #555;
          box-sizing: border-box;
          border-collapse: collapse;
          color: #333;
        }
        ul {
          display: none;
          position: absolute;
          top: 0;
          left: calc(30% - 2px);
          width: 70.8%;
          background-color: var(--ssbgc);
          li {
            width: 100%;
            height: 50px;
            line-height: 50px;
            text-align: center;
            font-size: 14px;
            border-bottom: 1px solid #333;
            border-left: 1px solid #333;
            box-sizing: border-box;
            a {
              color: #fff;
              width: 100%;
              height: 50px;
            }
          }
        }
        &.on > ul {
          display: block;
        }
      }
    }
  }
}
