#myreportslist {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  min-height: 580px;

  #list_title {
    width: 90%;
    height: 50px;
    text-align: center;
    line-height: 50px;
    margin-bottom: 10px;
    border-radius: 30px;
    background: var(--mbgc);
    box-shadow: 0 1px 2px #555;
    h2 {
      text-align: center;
      color: #fff;
    }
  }
  #report_items {
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 30px;
    width: 90%;
    padding: 30px 0;
    box-sizing: border-box;
    border-radius: 30px;
    background: var(--ssbgc);
    box-shadow: 0 1px 2px #555;
    .report_item {
      width: 90%;
      text-align: left;
      line-height: 50px;
      border-radius: 30px;
      padding: 10px;
      box-sizing: border-box;
      background: var(--sbgc);
      box-shadow: 0 1px 2px #555;
      .report_item_title {
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-direction: row;
        width: 100%;
        height: 35px;
        padding: 0 20px 0 30px;
        box-sizing: border-box;
        font-size: 20px;
        line-height: 30px;
        border-bottom: 2px solid #fff;
        a {
          svg {
            font-size: 20px;
            path {
              fill: #fff;
              transition: 0.5s;
            }
          }
          &:hover svg path {
            fill: #ddd;
          }
        }
      }
      .report_item_content {
        padding: 0 30px;
        box-sizing: border-box;
      }
    }
  }
}

