@mixin aligncenter {
  display: flex;
  justify-content: center;
  align-items: center;
}

#footer {
  position: relative;
  width: 100%;
  // height: 130px;
  background-color: antiquewhite;

  .footer_top {
    width: 100%;
    height: 30px;
    background: #333;
    ul {
      @include aligncenter;
      // gap: 10px;
      li {
        width: 33.333%;
        height: 30px;
        line-height: 30px;
        text-align: center;
        a {
          font-size: 12px;
          color: #fff;
        }
      }
    }
  }

  .footer_bottom {
    width: 100%;
    // height: 100px;
    padding: 10px;
    box-sizing: border-box;
    text-align: center;
    background-color: #555;
    p {
      font-size: 12px;
      font-weight: 200;
    }
  }
}
