#main {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;
  width: 100%;
  #main_notice {
    display: flex;
    justify-content: center;
    width: 90%;
    height: 30px;
    line-height: 30px;
    margin-bottom: 10px;
    border-radius: 15px;
    // border: 1px solid var(--mbgc);
    box-sizing: border-box;
    overflow: hidden;
    text-align: center;
    box-shadow: 0 1px 2px #555;
    #main_notice_title {
      flex: 2;
      background: var(--mbgc);
    }
    #main_notice_inner {
      flex: 8;
      color: #555;
      padding: 0 5px;
      box-sizing: border-box;
      background: #fff;
      overflow: hidden;
      // border: 1px solid var(--mbgc);
    }
  }
  #petition {
    position: relative;
    width: 90%;
    height: 30px;
    border-radius: 15px;
    // border: 1px solid var(--ssbgc);
    box-sizing: border-box;
    overflow: hidden;
    box-shadow: 0 1px 2px #555;

    #total {
      position: absolute;
      top: 0;
      right: 0;
      width: 100%;
      height: 100%;
      line-height: 30px;
      // border-radius: 30px;
      background: var(--ssbgc);
      padding-left: 50px;
      box-sizing: border-box;
      #year {
        position: absolute;
        top: 0;
        right: 0;
        width: 50%;
        height: 100%;
        // border-radius: 30px;
        background: var(--sbgc);
        padding-left: 50px;
        box-sizing: border-box;
        #today {
          position: absolute;
          top: 0;
          right: 0;
          width: 30%;
          height: 100%;
          // border-radius: 30px;
          background: var(--mbgc);
          padding-left: 20px;
          box-sizing: border-box;
        }
      }
    }
  }
  #banner {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 95%;
    height: 290px;
    margin: 10px 0;
    // overflow: hidden;
    ul {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 95%;
      height: 100%;
      border-radius: 15px;
      overflow: hidden;
      box-shadow: 0 1px 2px #555;

      li {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;
        a {
          width: 100%;
          height: 100%;
          background: url("../../image/banner1.jpg") no-repeat left top;
          background-size: cover;
        }
      }
    }
  }
  #mNav_disaster {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    gap: 30px;
    position: relative;
    width: 90%;
    height: 30px;
    text-align: center;
    line-height: 30px;

    margin-bottom: 10px;
    // border-radius: 15px;
    // overflow: hidden;
    & > div {
      width: 50%;
      height: 30px;
      border-radius: 15px;
      background: var(--sbgc);
      box-shadow: 0 1.5px 2px #555;

      & > a {
        width: 100%;
        height: 100%;
      }
    }
  }
  #mNav_user {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    gap: 30px;
    width: 90%;
    height: 100px;
    line-height: 25px;
    text-align: center;
    margin-top: 10px;
    & > div {
      width: 33.333%;
      height: 95%;
      border-radius: 15px;
      // overflow: hidden;
      background: var(--mbgc);
      box-shadow: 0 1.5px 2px #555;

      a {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        width: 100%;
        height: 100%;
        svg {
          font-size: 40px;
          background-color: transparent;
          path {
            // fill: ;
          }
        }
      }
    }
    #myonnuri {
    }
    #resultsearch {
    }
    #useguide {
    }
  }
  #mNav_police {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    gap: 30px;
    position: relative;
    width: 90%;
    height: 30px;
    text-align: center;
    line-height: 30px;
    // border-radius: 30px;
    // overflow: hidden;
    & > div {
      width: 50%;
      height: 30px;
      border-radius: 15px;
      background: var(--sbgc);
      box-shadow: 0 1.5px 2px #555;

      & > a {
        width: 100%;
        height: 100%;
      }
    }
  }
}
