#login {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  min-height: 600px;
  h2 {
    width: 90%;
    height: 50px;
    line-height: 50px;
    color: #fff;
    text-align: center;
    letter-spacing: 2px;
    border-radius: 30px;
    background-color: var(--mbgc);
  }
  form {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 90%;
    padding: 10px;
    box-sizing: border-box;
    input {
      width: 310px;
      height: 50px;
      padding: 0 10px;
      font-size: 16px;
      color: #555;
      border-bottom: 1px solid var(--sbgc);
      box-sizing: border-box;
      &#login_id {
        margin-bottom: 5px;
      }
      &#login_pw {
        margin-bottom: 10px;
      }
      &#login_submit {
        height: 30px;
        color: #fff;
        border-radius: 30px;
        background-color: var(--sbgc);
        cursor: pointer;
      }
    }
  }
  .login_other {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    a {
      width: 150px;
      height: 30px;
      line-height: 30px;
      text-align: center;
      border-radius: 30px;
      background-color: var(--ssbgc);
      span {
        font-size: 14px;
        color: #fff;
      }
      &:nth-child(1) {
        margin-right: 10px;
      }
    }
  }
}
