#notice {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  color: #555;
  #notice_title {
    width: 90%;
    height: 50px;
    line-height: 50px;
    margin-bottom: 10px;
    border-radius: 30px;
    background: var(--sbgc);
    box-shadow: 0 1px 2px #555;
    h2 {
      text-align: center;
      color: #fff;
      font-weight: 400;
    }
  }
  table.notice_table {
    width: 90%;
    thead {
      tr {
        th {
          height: 30px;
          line-height: 30px;
          color: #555;
          border-bottom: 1px solid #aaa;
          &:nth-child(1) {
            width: 15%;
          }
          &:nth-child(2) {
            width: 65%;
          }
          &:nth-child(3) {
            width: 20%;
          }
        }
      }
    }
    tbody {
      tr {
        td {
          height: 30px;
          line-height: 30px;
          color: #555;
          text-align: center;
          padding: 0 10px;
          border-bottom: 1px solid #eee;
          &:nth-child(1) {
            width: 15%;
          }
          &:nth-child(2) {
            width: 65%;
            text-align: left;
            overflow: hidden;
            text-overflow: ellipsis;
          }
          &:nth-child(3) {
            width: 20%;
          }
        }
      }
    }
  }

  form {
    div {
      color: #555;
    }
    select {
      width: 80px;
      height: 30px;
      text-align: center;
      line-height: 30px;
      color: #555;
      margin-right: 5px;
      option {
        color: #555;
      }
    }
    input[type="text"] {
      width: 200px;
      height: 30px;
      line-height: 30px;
      padding: 0 10px;
      color: #555;
      border: 1px solid var(--ssbgc);
      border-radius: 10px;
      box-sizing: border-box;
      &::placeholder {
        color: #555;
      }
    }
    input[type="submit"] {
      width: 80px;
      height: 30px;
      margin-left: 5px;
      line-height: 30px;
      background-color: var(--ssbgc);
      border-radius: 10px;
    }
  }
}
