:root {
  --mbgc: #597CF6;
  --sbgc: #809CFF;
  --ssbgc: #98AEFD;
  /* width: 430px; */
  /* height: auto; */
}

* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  -webkit-user-select: none;
  user-select: none;
  font-family: "나눔바른고딕OTF", "맑은 고딕", Arial, Helvetica, sans-serif;
  color: #fff;
}

ul,
li {
  list-style: none;
}

a {
  text-decoration: none;
  display: block;
}