#theme {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  min-height: 580px;
  #theme_title {
    width: 90%;
    height: 50px;
    line-height: 50px;
    margin-bottom: 10px;
    border-radius: 30px;
    background: var(--sbgc);
    box-shadow: 0 1px 2px #555;
    h2 {
      text-align: center;
      color: #fff;
      font-weight: 400;
    }
  }
  #theme_inner {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 30px;
    width: 90%;
    padding: 30px 0;
    box-sizing: border-box;
    border-radius: 30px;
    background: var(--ssbgc);
    box-shadow: 0 1px 2px #555;
    .theme_inner_item {
      width: 80%;
      height: 50px;
      text-align: center;
      line-height: 50px;
      border-radius: 30px;
      background: var(--sbgc);
      box-shadow: 0 1px 2px #555;
      a {
        color: #fff;
      }
    }
  }
}

// 공통 스타일
.theme {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  .theme_title {
    width: 90%;
    height: 50px;
    line-height: 50px;
    margin-bottom: 10px;
    border-radius: 30px;
    background: var(--sbgc);
    box-shadow: 0 1px 2px #555;
    h2 {
      text-align: center;
      color: #fff;
      font-weight: 400;
    }
  }
  .theme_inner {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 30px;
    width: 90%;
    padding: 10px 0;
    box-sizing: border-box;
    border-radius: 30px;
    background: var(--ssbgc);
    box-shadow: 0 1px 2px #555;
    form {
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      #case {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: row;
        gap: 30px;
        width: 90%;
        padding: 10px 0;
        box-sizing: border-box;
        border-radius: 30px;
        background: var(--sbgc);
        input {
          position: relative;
          font-size: 14px;
          margin-left: 10px;
          letter-spacing: 2px;
          text-align: center;
          color: #fff;
          background: transparent;
          &::-webkit-calendar-picker-indicator {
            position: absolute;
            left: -50px;
            width: 200%;
            height: 100%;
            color: transparent;
            fill: transparent;
            background-color: transparent;
            cursor: pointer;
          }
        }
      }
      #case_report {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        textarea#input_report {
          width: 90%;
          height: 270px;
          margin: 10px 0;
          color: #fff;
          font-size: 20px;
          border-radius: 30px;
          padding: 30px;
          outline: none;
          resize: none;
          box-sizing: border-box;
          background: var(--sbgc);
          overflow: hidden;
          box-shadow: 0 1px 2px #555;
          &::placeholder {
            font-size: 16px;
            color: #fff;
          }
        }
      }
      input {
        box-shadow: 0 1px 2px #555;
      }
      input#theme_adress {
        display: block;
        width: 90%;
        height: 30px;
        font-size: 20px;
        margin-bottom: 10px;
        border-radius: 30px;
        padding: 0 20px;
        box-sizing: border-box;
        text-align: center;
        background: var(--sbgc);
        &::placeholder {
          font-size: 16px;
          color: #fff;
        }
      }
      div#file_add {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        width: 90%;
        height: 200px;
        background: var(--sbgc);
        border-radius: 30px;
        overflow: hidden;
        box-shadow: 0 1px 2px #555;

        h3 {
          width: 100%;
          height: 50px;
          line-height: 50px;
          text-align: center;
          font-weight: 400;
        }
        #file_field {
          display: flex;
          justify-content: center;
          align-items: center;
          flex-direction: row;
          width: 100%;
          height: 100%;
          #file_add_field {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 30%;
            height: 100%;
            #file_label {
              width: 120px;
              height: 30px;
              font-size: 20px;
              line-height: 30px;
              text-align: center;
              cursor: pointer;
            }
            input#input_file {
              display: none;
            }
          }
          #file_list {
            width: 70%;
            height: 100%;
            padding: 0 10px;
            box-sizing: border-box;
            #file_list_title {
              position: relative;
              top: 0;
              width: 100%;
              height: 30px;
              line-height: 30px;
              top: 0;
              text-align: center;
            }
            .file_item {
              display: block;
              width: 100%;
              height: 25px;
              line-height: 25px;
              overflow: hidden;
              text-overflow: ellipsis;
              white-space: nowrap;
            }
          }
        }
      }
      input#input_submit {
        width: 120px;
        height: 30px;
        font-size: 20px;
        color: #fff;
        margin-top: 10px;
        border-radius: 30px;
        background-color: var(--sbgc);
        cursor: pointer;
      }
    }
  }
}
