#notice_items {
  height: 30px;
  .swiper-slide {
    height: 30px;
    transition: 0.5s;
    a {
      padding: 0 5px;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      color: #555;
      box-sizing: border-box;
      text-shadow: none;
    }
  }
}
