#MainNav {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 100%;
  height: 80px;
  // margin-top: 10px;
  text-align: center;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  background: linear-gradient(transparent 50%, var(--mbgc) 50%);
  ul {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    width: 100%;
    height: 100%;
    li {
      position: relative;
      width: 20%;
      height: 100%;
      &:nth-child(1):after {
        position: absolute;
        bottom: 20px;
        content: "";
        display: block;
        width: 20px;
        height: 20px;
        border-top-left-radius: 20px;
        background-color: var(--mbgc);
        z-index: 998;
      }
      &:nth-child(1)::before {
        position: absolute;
        bottom: 20px;
        content: "";
        display: block;
        width: 20px;
        height: 20px;
        background-color: #fff;
        z-index: 998;
      }
      &:last-child:after {
        position: absolute;
        right: 0;
        bottom: 20px;
        content: "";
        display: block;
        width: 20px;
        height: 20px;
        border-top-right-radius: 20px;
        background-color: var(--mbgc);
        z-index: 998;
      }
      &:last-child::before {
        position: absolute;
        right: 0;
        bottom: 20px;
        content: "";
        display: block;
        width: 20px;
        height: 20px;
        background-color: #fff;
        z-index: 998;
      }
      a {
        position: absolute;
        bottom: 5px;
        width: 100%;
        height: 50%;
        text-shadow: 0 2px 2px #555;
        line-height: 50px;
        z-index: 999;
        span {
          display: block;
          width: 100%;
          height: 100%;
        }
        &.on {
          left: 0;
          right: 0;
          margin: auto;
          width: 70px;
          height: 70px;
          border-radius: 50%;
          border: 5px solid #fff;
          box-sizing: border-box;
          box-shadow: 0 2px 2px #333;
          background-color: var(--mbgc);
          z-index: 999;
          & > span {
            line-height: 65px;
            font-size: 20px;
          }
        }
      }
    }
  }

  #navselect {
    position: absolute;
    top: 0;
    width: 70px;
    height: 70px;
    background: var(--mbgc);
    border: 5px solid #fff;
    border-radius: 50%;
    box-sizing: border-box;
    color: #fff;
    line-height: 60px;
    text-align: center;
  }
}
