#guidemain {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  // min-height: 580px;
  #guide_title {
    width: 90%;
    height: 50px;
    text-align: center;
    line-height: 50px;
    margin-bottom: 10px;
    border-radius: 30px;
    background: var(--mbgc);
    box-shadow: 0 1px 2px #555;

    h2 {
      text-align: center;
      color: #fff;
    }
  }
  #guide_items {
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 30px;
    width: 90%;
    padding: 30px 0;
    box-sizing: border-box;
    border-radius: 30px;
    background: var(--ssbgc);
    box-shadow: 0 1px 2px #555;
    .guide_item {
      width: 90%;
      line-height: 50px;
      border-radius: 30px;
      padding: 10px;
      box-sizing: border-box;
      background: var(--sbgc);
      box-shadow: 0 1px 2px #555;
      .guide_item_title {
        width: 100%;
        height: 30px;
        text-align: center;
        padding: 0 30px;
        box-sizing: border-box;
        font-size: 20px;
        line-height: 30px;
        a {
          color: #fff;
        }
      }
    }
  }
}

.detail_guide {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  .detail_guide_title {
    width: 90%;
    height: 50px;
    text-align: center;
    line-height: 50px;
    margin-bottom: 10px;
    border-radius: 30px;
    background: var(--mbgc);
    box-shadow: 0 1px 2px #555;
    h2 {
      text-align: center;
      color: #fff;
      font-weight: 400;
    }
  }
  .detail_guide_items {
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 30px;
    width: 90%;
    padding: 30px 0;
    box-sizing: border-box;
    border-radius: 30px;
    background: var(--ssbgc);
    box-shadow: 0 1px 2px #555;
    .detail_guide_item {
      width: 90%;
      line-height: 50px;
      border-radius: 30px;
      padding: 10px;
      box-sizing: border-box;
      background: var(--sbgc);
      box-shadow: 0 1px 2px #555;
      .detail_guide_item_title {
        width: 100%;
        height: 30px;
        text-align: center;
        padding: 0 30px;
        box-sizing: border-box;
        font-size: 20px;
        line-height: 30px;
      }
      .detail_guide_item_content {
        width: 100%;
        text-align: center;
        padding: 0 10px;
        box-sizing: border-box;
        font-size: 14px;
        line-height: 22px;
      }
    }
  }
}
